'}\">汽修厂列表</router-link>-->\n\t\t\t<!--<router-link :to=\"{name:'shopcash_apply'}\">汽修厂提现</router-link>-->\n\t\t\t<!--<router-link :to=\"{name:'shoplist_close'}\" class=\"cur\">关停列表</router-link>-->\n\t\t\t<!--<router-link :to=\"{name:'shoplist_cancel'}\">取消合作</router-link>-->\n\t\t\t<router-link :class=\"seCurId==item.id? 'cur':''\" v-for=\"item in authList\" :key=\"item.id\" :to=\"{path:item.action,query:{id:curId}}\" :v-key=\"item.id\">{{item.name}}\n\t\t\t</router-link>\n\t\t</ctbHead>\n\t\t<div class=\"container\">\n\t\t\t<div class=\"quote\">\n\t\t\t\t<div class=\"quote-ele\"><i></i>物料审核</div>\n\t\t\t</div>\n\t\t\t<table class=\"table\">\n\t\t\t\t<thead>\n\t\t\t\t\t<tr>\n\t\t\t\t\t\t<th>维修厂</th>\n\t\t\t\t\t\t<th>联系电话</th>\n\t\t\t\t\t\t<th>负责人</th>\n\t\t\t\t\t\t<th>申请时间</th>\n\t\t\t\t\t\t<th>操作</th>\n\t\t\t\t\t</tr>\n\t\t\t\t</thead>\n\t\t\t\t<tbody>\n\t\t\t\t\t<tr v-for=\"item in list\">\n\t\t\t\t\t\t<td class=\"t-l\">{{item.company}}</td>\n\t\t\t\t\t\t<td>{{item.phone}}</td>\n\t\t\t\t\t\t<td>{{item.leader}}</td>\n\t\t\t\t\t\t<td>{{item.create_time}}</td>\n\t\t\t\t\t\t<td>\n\t\t\t\t\t\t\t<a href=\"javascript:;\" @click=\"eachData=item,detailsDialogVisible=true,adopt(item.id)\">通过</a>\n\n\t\t\t\t\t\t</td>\n\t\t\t\t\t</tr>\n\t\t\t\t</tbody>\n\t\t\t</table>\n            <el-dialog\n                title=\"详情\"\n                :visible.sync=\"detailsDialogVisible\"\n                center>\n                <div class=\"details\">\n                    <p>\n\t\t\t\t\t\t<span>维修厂 : </span>\n\t\t\t\t\t\t<span>{{detailsList.company}}</span>\n\t\t\t\t\t</p>\n\t\t\t\t\t<p>\n\t\t\t\t\t\t<span>联系电话 : </span>\n\t\t\t\t\t\t<span>{{detailsList.phone}}</span>\n\t\t\t\t\t</p>\n\t\t\t\t\t<p>\n\t\t\t\t\t\t<span>详细地址 : </span>\n\t\t\t\t\t\t<span>{{`${detailsList.province}${detailsList.city}${detailsList.county}${detailsList.address}`}}</span>\n\t\t\t\t\t</p>\n\n                    <el-table :data=\"detailsList.detail\" border style=\"margin:20px 0\">\n                        <el-table-column label=\"数量\" prop=\"num\" align=\"center\" ></el-table-column>\n                        <el-table-column label=\"名称\" prop=\"materiel\" align=\"center\" ></el-table-column>\n                         <el-table-column label=\"物料备注\" prop=\"remarks\" align=\"center\" ></el-table-column>\n                    </el-table>\n                </div>\n                <span slot=\"footer\" class=\"dialog-footer\">\n                    <el-button type=\"primary\" @click=\"Sure()\">确定</el-button>\n                </span>\n            </el-dialog>\n\n\t\t\t<!-- 分页 -->\n\t\t\t<div class=\"page_center\" v-show=\"pageCount&&pageCount>1\">\n\t\t\t\t<paging :page-count=\"pageCount\" :page=\"page\" @index=\"paging\"></paging>\n\t\t\t\t<!--分页的组件-->\n\t\t\t</div>\n\t\t</div>\n\n\t</div>\n</template>\n<script type=\"text/ecmascript-6\">\n\timport page from '../common.js';\n\texport default {\n\t\tdata() {\n\t\t\t\treturn {\n\t\t\t\t\tlist: [],\n\t\t\t\t\ttoken: window.sessionStorage.getItem('bbytoken'),\n\t\t\t\t\t//分页数据\n\t\t\t\t\tpage: 1, //显示的页码数\n\t\t\t\t\ttotalpage: 1, //总页数\n\t\t\t\t\tindex: 1, //点击的当前页，\n\t\t\t\t\tendpage: 1, //当前一组页码的最大值\n\t\t\t\t\tarr: [], //默认显示 1 2 3 4 5页\n\t\t\t\t\tn: 1, //当前点击的页码的位置\n\t\t\t\t\tpageCount: 1,\n\t\t\t\t\treason: '',\n\t\t\t\t\tid: '',\n\t\t\t\t\tauthList: [],\n\t\t\t\t\tcurId: 0,\n                    seCurId: 0,\n                    detailsDialogVisible:false,\n                    detailsList:[],\n                    eachData:{},\n                    reason:'',\n\n\t\t\t\t}\n\t\t\t},\n\t\t\tmethods: {\n\t\t\t\tgo: page.go,\n\t\t\t\tfirst: page.first,\n\t\t\t\tend: page.end,\n\t\t\t\tprev: page.prev,\n\t\t\t\tnext: page.next,\n\t\t\t\tshowpage: page.showpage,\n\t\t\t\tpaging(e) {\n\t\t\t\t\tthis.page = e;\n\t\t\t\t\tthis.init()\n\t\t\t\t},\n\t\t\t\tinit() {\n\t\t\t\t\tthis.$axios.post('admin/ShopMateriel/list', {\n\t\t\t\t\t\t\ttoken: this.token,\n                            page: this.page,\n                            status:0\n\t\t\t\t\t\t})\n\t\t\t\t\t\t.then(res => {\n\t\t\t\t\t\t\t\n\t\t\t\t\t\t\tif(res.data.code == 1) {\n\t\t\t\t\t\t\t\tthis.list = res.data.data.list;\n\t\t\t\t\t\t\t\tthis.showpage(res.data.data);\n\t\t\t\t\t\t\t\tthis.pageCount = res.data.data.rows\n\t\t\t\t\t\t\t}else{\n                                this.list=[]\n\t\t\t\t\t\t\t\tlayer.msg(res.data.msg)\n\t\t\t\t\t\t\t}\n\t\t\t\t\t\t})\n\t\t\t\t\t\t.catch(err => console.log(err))\n\t\t\t\t},\n\t\t\t\tshowreason: function(id) {\n\t\t\t\t\tthis.id = id;\n\t\t\t\t\tthis.$axios.post('admin/ShopDownList/dowReason', {\n\t\t\t\t\t\t\ttoken: this.token,\n\t\t\t\t\t\t\tid: id\n\t\t\t\t\t\t})\n\t\t\t\t\t\t.then(res => {\n\t\t\t\t\t\t\t\n\t\t\t\t\t\t\tlayer.open({\n\t\t\t\t\t\t\t\ttype: 0,\n\t\t\t\t\t\t\t\tarea: ['40%', '30%'],\n\t\t\t\t\t\t\t\ttitle: ['关停理由', 'height:60px;background-color:#98D1F9;color:white;text-align:center;line-height:60px;font-size:20px;'],\n\t\t\t\t\t\t\t\tshadeClose: true,\n\t\t\t\t\t\t\t\tshade: 0.8,\n\t\t\t\t\t\t\t\tcontent: res.data.data\n\t\t\t\t\t\t\t})\n\t\t\t\t\t\t}).catch(err => console.log(err))\n\t\t\t\t},\n\t\t\t   async adopt(id) {\n\t\t\t\t\ttry{\n                  const res = await this.$axios.post('admin/ShopMateriel/detail',{token:this.token,id:id});\n                  if(res.data.code==1){\n                      this.detailsList=res.data.data.list;\n                  }else{\n                       this.detailsList=[]\n                  }\n              }catch(err){\n                   throw(err)\n              }\n          },\n          async Sure(){\n              try{\n                 const res = await this.$axios.post('admin/ShopMateriel/adopt',{token:this.token,id:this.eachData.id,phone:this.eachData.phone})\n                 if(res.data.code==1){\n                     this.detailsDialogVisible=false,\n                     this.$message({message:res.data.msg,type:'success'})\n                     this.init()\n                 }else{\n                     this.$message.error(res.data.msg)\n                 }\n              } catch(err){\n                  throw(err)\n              }\n          },\n\n\t\t\t\tcancel() {\n\t\t\t\t\tlayer.close(this.layerIndex);\n\t\t\t\t},\n\t\t\t\tpass() {\n\t\t\t\t\tif(!this.reason) {\n\t\t\t\t\t\tlayer.msg('请输入开通理由！');\n\t\t\t\t\t\treturn;\n\t\t\t\t\t}\n\t\t\t\t\tthis.$axios.post('admin/ShopDownList/openAgain', {\n\t\t\t\t\t\t\ttoken: this.token,\n\t\t\t\t\t\t\tid: this.id,\n\t\t\t\t\t\t\treason: this.reason\n\t\t\t\t\t\t})\n\t\t\t\t\t\t.then(res => {\n\t\t\t\t\t\t\t\n\t\t\t\t\t\t\tif(res.data.code == 1) {\n\t\t\t\t\t\t\t\tlayer.msg(res.data.msg, {\n\t\t\t\t\t\t\t\t\ttime: 500\n\t\t\t\t\t\t\t\t}, function() {\n\t\t\t\t\t\t\t\t\tlayer.closeAll();\n\t\t\t\t\t\t\t\t});\n\t\t\t\t\t\t\t\tthis.init();\n\t\t\t\t\t\t\t} else {\n\t\t\t\t\t\t\t\tlayer.closeAll();\n\t\t\t\t\t\t\t\tthis.$alert('开通失败', '提示', {\n\t\t\t\t\t\t\t\t\ttype: 'error'\n\t\t\t\t\t\t\t\t});\n\t\t\t\t\t\t\t}\n\t\t\t\t\t\t})\n\t\t\t\t\t\t.catch(err => alert(err))\n\t\t\t\t}\n\t\t\t},\n\t\t\tcreated() {\n\t\t\t\tthis.init();\n\t\t\t},\n\t\t\tmounted() {\n\t\t\t\tvar id = this.$route.query.id;\n\t\t\t\tthis.curId = id;\n\t\t\t\tthis.$axios.post('admin/Auth/erAuth', {\n\t\t\t\t\t\ttoken: window.sessionStorage.getItem('bbytoken'),\n\t\t\t\t\t\tid: id\n\t\t\t\t\t})\n\t\t\t\t\t.then(res => {\n\t\t\t\t\t\t\n\t\t\t\t\t\tif(res.data.code == 1) {\n\t\t\t\t\t\t\tvar arr = res.data.data;\n\t\t\t\t\t\t\tfor(var i = 0; i < arr.length; i++) {\n\t\t\t\t\t\t\t\tif(arr[i].name == '物料审核') {\n\t\t\t\t\t\t\t\t\tthis.seCurId = arr[i].id;\n\t\t\t\t\t\t\t\t}\n\t\t\t\t\t\t\t\tif(arr[i].son) {\n\t\t\t\t\t\t\t\t\tfor(var j = 0; j < arr[i].son.length; j++) {\n\t\t\t\t\t\t\t\t\t\tif(arr[i].action != arr[i].son[j].action) {\n\t\t\t\t\t\t\t\t\t\t\tarr[i].action = arr[i].son[0].action;\n\t\t\t\t\t\t\t\t\t\t}\n\t\t\t\t\t\t\t\t\t}\n\t\t\t\t\t\t\t\t}\n\t\t\t\t\t\t\t}\n\t\t\t\t\t\t\tthis.authList = arr;\n\t\t\t\t\t\t} else {\n\t\t\t\t\t\t\tthis.$alert(res.data.msg, '提示', {\n\t\t\t\t\t\t\t\ttype: 'error'\n\t\t\t\t\t\t\t});\n\t\t\t\t\t\t}\n\t\t\t\t\t})\n\t\t\t\t\t.catch(err => {\n\t\t\t\t\t\talert(err);\n\t\t\t\t\t})\n\t\t\t},\n\t\t\twatch: {\n\t\t\t\tindex() {\n\t\t\t\t\tthis.init();\n\t\t\t\t}\n\t\t\t}\n\t}\n</script>\n<style scoped>\n\t.laybox {\n\t\ttext-align: center;\n\t\tpadding: 50px 68px 50px 36px;\n\t\tfont-size: 16px;\n\t\toverflow: hidden;\n\t}\n\n\t.laybox textarea {\n\t\toutline: none;\n\t\tresize: none;\n\t    width: 80%;\n\t    flex-flow: 1;\n\t\theight: 250px;\n\t\tborder: 1px dashed #04BBFC;\n\t\tmargin-bottom: 60px;\n\n\t\tborder-radius: 15px;\n\t}\n\t.laybox>p{\n\t\tdisplay: flex;\n\t\tflex-flow: 1;\n\n\t}\n\n\tdiv.bot button {\n\t\twidth: 110px;\n\t\theight: 36px;\n\t\tborder-radius: 18px;\n\t\tbox-shadow: 4px 0px 6px rgba(177, 42, 19, 0.2);\n\t\tborder: none;\n\t\tcolor: white;\n\t}\n\n\tbutton.reject {\n\t\tbackground: rgba(237, 81, 55, 1);\n\t\tmargin-right: 30%;\n\t}\n\n\tbutton.pass {\n\t\tbackground-color: #20A0FF;\n\t}\n\t.details{\n\t\tpadding: 10px 0;\n\t}\n\t.details>p>span:first-child{\n\t   width: 70px;\n\t   text-align: right;\n\t   display: inline-block\n\t}\n</style>\n"],"sourceRoot":""}]);

